export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			picUrl: null,
			user: null
		}
	},
	methods: {
		// 选择图片
		chooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sourceType: ['album'], //从相册选择
				success: (res) => {
					console.log(res);
					this.picUrl = res.tempFiles[0].path
				},
				fail: (err) => {
					console.log(err);
				}
			});
		},
		// 上传图片
		updatePic() {
			if (!this.user || !this.picUrl) {
				uni.showToast({
					icon: "none",
					title: "图片不存在或信息不完整",
					duration: 800
				})
				return
			}
			uni.showLoading({
				title: '上传中...'
			});
			uni.uploadFile({
				url: this.$api.updateImage({
					userId: this.user.id
				}),
				filePath: this.picUrl,
				name: 'file',
				header: {
					"headerUserId": this.user.id,
					"headerUserToken": this.user.userUniqueToken
				},
				formData: {},
				success: (uploadFileRes) => {
					let res = JSON.parse(uploadFileRes.data)
					uni.hideLoading()
					if (res.status === 200) {
						uni.showToast({
							title: '上传成功',
							duration: 800
						});
						setTimeout(() => {
							this.picUrl = null
							uni.setStorageSync('userinfo', JSON.stringify(res.data))
							uni.navigateTo({
								url: '../settings/settings',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}, 800)
					}
					console.log(res);
				}
			})
		}
	},
	mounted() {

	},
	onLoad() {
		this.user = JSON.parse(uni.getStorageSync('userinfo'))
		console.log(this.user);
		uni.showModal({
			title: '选取本地照片',
			content: '点击确认选取照片',
			success: (res) => {
				if (res.confirm) {
					this.chooseImage()
				}
			}
		})
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
